"use strict";
import React, {Component, PropTypes} from 'react'
import './Invitation.scss'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import Constant from '../../../components/lib/Constant.js'
import Params from '../../../components/lib/QueryParams.js'
import en from '../../../locale/en';
import zh from '../../../locale/zh';
import $ from 'n-zepto'
import {InputLine, SelectLine, ImageLine, CheckBoxLine, RadioLine} from '../../../components/lib/BaseForm'
import {Menu, FormattedMessage} from '../../../components/lib/BaseComponent'
import Header from '../../../components/Header/Header';
import Footer from '../../../components/Footer/Footer';
import ImageSrc from '../assets/image.png'
import Position from '../assets/work.png'
import Company from '../assets/school.png'
import Email from '../assets/email.png'
import Name from '../assets/name.png'
import PhoneIcon from '../assets/phone.png'
import Photo from '../assets/photo.png'
import Loading from '../assets/loading.gif'
import {browserHistory} from 'react-router'

let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class Title extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "24px",
            fontWeight: "600",
            margin: "60px 0 20px 0"
        } : {
            fontSize: "16px",
            fontWeight: "600",
            margin: "40px 20px 20px 20px"
        }}>
            {this.props.children}
        </div>
    }
}

class Tip extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "12px",
            paddingBottom: "80px"
        } : {
            fontSize: "12px",
            margin: "0 20px"
        }}>
            <FormattedMessage id="student_check_tip"/>
        </div>
    }
}

class Form extends Component {
    constructor(props) {
        super(props);
        this.type = null
        this.form_names = ['name', 'phone', 'email', 'sex', 'ageLevel', 'headImg', 'company', 'companySize', 'position', 'positionSelect', 'whetherUseAi',
            'useAiSelect', 'whetherUseBaiduAi', 'useBaiduAiSelect', 'futureAiPlanSelect', 'whetherFirstAttend', 'likeTribuneSelect',
            'gainsResultSelect'];
        this.time = {
            "seconds": 60
        };
        this.form_data = {};
        this.error = {
            "text": 'sure'
        }
        this.sex = [{value: '男', text: messages[localStorage['language']]['sex1']}, {
            value: '女',
            text: messages[localStorage['language']]['sex2']
        }];
        this.noOrYes = [{value: '1', text: messages[localStorage['language']]['yes']}, {
            value: '0',
            text: messages[localStorage['language']]['no']
        }]
        this.typeList = [{value: '0', text: messages[localStorage['language']]['developer_ticket_19']}, {
            value: '3',
            text: messages[localStorage['language']]['sub_forum_ticket_2019']
        }]
        this.ageLevel = [];
        this.companySize = [];
        this.positionSelect = [];

        for (var i = 1; i < 6; i++) {
            this.ageLevel.push({
                value: i + '',
                text: messages[localStorage['language']]['ageLevel' + i]
            })
        }
        for (var i = 1; i < 8; i++) {
            this.companySize.push({
                value: i + '',
                text: messages[localStorage['language']]['companySize' + i]
            })
        }
        for (var i = 1; i < 14; i++) {
            this.positionSelect.push({
                value: i + '',
                text: messages[localStorage['language']]['positionSelect' + i]
            })
        }
        //多选
        this.useAiSelectData = [];
        this.futureAiPlanSelectData = [];
        this.likeTribuneSelectData = [];
        this.gainsResultSelectData = [];
        this.positonSelectData = [];
        this.useBaiduAISelectData = [];
        for (let i = 1; i < 16; i++) {
            this.useBaiduAISelectData.push({
                'value': i + '',
                'text': 'useBaiduAiSelect' + i
            })
        }
        for (let i = 1; i < 14; i++) {
            this.positonSelectData.push({
                'value': i + '',
                'text': 'positionSelect' + i
            })
        }
        for (let i = 1; i < 13; i++) {
            this.useAiSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 14; i++) {
            this.futureAiPlanSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 10; i++) {
            this.likeTribuneSelectData.push({
                'value': i + '',
                'text': 'likeTribuneSelect' + i
            })
        }
        for (let i = 1; i < 7; i++) {
            this.gainsResultSelectData.push({
                'value': i + '',
                'text': 'gainsResultSelect' + i
            })
        }

    }

    componentDidMount() {
        if (Params('c')) {
            $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
                if (data.error_no !== '0') {
                    setTimeout(() => {
                        if (IsPcOrNot.get()) {
                            window.instance.show();
                        }
                        else {
                            $('#login-wrap').css("display", "block");
                            $('#login').css("display", "block");
                        }
                    }, 1000);
                }
                else {
                    $.get(Constant.url + 'queryc.do?c=' + Params('c'), (data) => {
                        // console.log(data);
                        if (data.error_no !== '0' && data.error_no !== '3') {
                            this.error.text = data.data;
                            this.setState(this.error);
                            this.refs.Menu.open();
                        }
                        else if (data.error_no === '3') {
                            this.error.text = data.data;
                            this.setState(this.error);
                            this.refs.Menu.open();
                            setTimeout(() => {
                                browserHistory.push(Constant.router_preffix + '/my_tickets');
                            }, 3000);
                        }
                    });
                }
            })
        } else {
            browserHistory.push(Constant.router_preffix + '/')
        }
    }

    submit = () => {
        let flag = true;
        for (let i = 0; i < this.form_names.length; i++) {
            if (this.refs[this.form_names[i]] !== undefined) {
                if (!this.refs[this.form_names[i]].valid()) {
                    flag = false;
                    break;
                }
            }
        }
        if (flag) {
            if (Params('c')) {
                this.form_data.c = Params('c');
            }
            if(Params('type')) {
                this.form_data.type = Params('type');
            }
            $.post(Constant.url + 'invitedTicket.do', this.form_data, (data) => {
                if (data.error_no == '0') {
                    //TODO success call back
                    this.error.text = data.data;
                    this.setState(this.error);
                    browserHistory.push(Constant.router_preffix + '/my_tickets')
                }
                else {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
            });
        }
    };
    refresh = () => {
        this.setState(this.form_data);
    }

    render() {
        return <div>
            <Title>
                <FormattedMessage id="members_information"/>
            </Title>
            <Menu ref="Menu" text={this.error.text}/>
            <Tip/>
            <table cellPadding='0' cellSpacing='0' className={IsPcOrNot.get() ? "form-table" : 'm-table'}>
                <tbody>
                <tr>
                    <td>
                        <FormattedMessage id="base_info"/>
                    </td>
                    <td>
                        <InputLine form={this.form_data} ref='name' text="name" icon={Name}
                                   name='name' valid="notNull||name"/>

                        <InputLine form={this.form_data} ref='phone' text="phone" icon={PhoneIcon}
                                   name='phone' valid="notNull"/>

                        <InputLine form={this.form_data} ref='email' text="email" icon={Email}
                                   name='email'
                                   valid="notNull"/>
                        <RadioLine form={this.form_data} ref='sex' text="sex" data={this.sex}
                                   name='sex' valid="notNull"/>

                        <SelectLine form={this.form_data} ref='ageLevel' text="ageLevel"
                                    data={this.ageLevel} name='ageLevel' valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="upload_avatar"/>
                    </td>
                    <td>
                        <ImageLine form={this.form_data} photo={Photo} ImageSrc={ImageSrc} loading={Loading}
                                   ref='headImg' text="card_picture" name='headImg'
                                   valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="profession_info"/>
                    </td>
                    <td>
                        <InputLine form={this.form_data} ref='company' text="company" icon={Company}
                                   name='company' valid="notNull"/>

                        <SelectLine form={this.form_data} ref='companySize' data={this.companySize}
                                    text="companySize" name='companySize'
                                    valid="notNull"/>

                        <InputLine form={this.form_data} ref='position' text="job" icon={Position}
                                   name='position' valid="notNull"/>

                        <SelectLine form={this.form_data} ref='positionSelect' data={this.positionSelect}
                                    text="positionSelect" name='positionSelect' valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="about_ai"/>
                    </td>
                    <td>
                        <RadioLine form={this.form_data} ref='whetherUseAi'
                                   refresh={this.refresh.bind(this)}
                                   text="whetherUseAi" data={this.noOrYes}
                                   name='whetherUseAi' valid="notNull"/>
                        {
                            this.form_data['whetherUseAi'] == "1" ?
                                <CheckBoxLine form={this.form_data} ref='useAiSelect' text="useAiSelect"
                                              name='useAiSelect' data={this.useAiSelectData}
                                              valid="notNull"/> : null

                        }
                        {
                            this.form_data['whetherUseAi'] == '1' ?
                                <RadioLine form={this.form_data} ref='whetherUseBaiduAi'
                                           refresh={this.refresh.bind(this)} data={this.noOrYes}
                                           text="whetherUseBaiduAi" name='whetherUseBaiduAi'
                                           valid="notNull"/> : null

                        }
                        {
                            (this.form_data['whetherUseBaiduAi'] == '1' && this.form_data['whetherUseAi'] == "1") ?
                                <CheckBoxLine form={this.form_data} ref='useBaiduAiSelect'
                                              text="useBaiduAiSelect"
                                              name='useBaiduAiSelect' data={this.useBaiduAISelectData}
                                              valid="notNull"/> : null

                        }
                        <CheckBoxLine form={this.form_data} ref='futureAiPlanSelect' text="futureAiPlanSelect"
                                      name='futureAiPlanSelect' data={this.futureAiPlanSelectData}
                                      valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="join_info"/>
                    </td>
                    <td>
                        <RadioLine form={this.form_data} ref='whetherFirstAttend' text="whetherFirstAttend"
                                   name='whetherFirstAttend' data={this.noOrYes}
                                   valid="notNull"/>
                        <CheckBoxLine form={this.form_data} ref='likeTribuneSelect' text="likeTribuneSelect"
                                      name='likeTribuneSelect' data={this.likeTribuneSelectData}
                                      valid="notNull"/>
                        <CheckBoxLine form={this.form_data} ref='gainsResultSelect' text="gainsResultSelect"
                                      name='gainsResultSelect' data={this.gainsResultSelectData}
                                      valid="notNull"/>
                    </td>
                </tr>
                </tbody>
            </table>
            <div className="tribune-selector" style={{marginTop: IsPcOrNot.get() ? '60px' : '0'}}>
                <div style={{margin: IsPcOrNot.get() ? "40px 0 80px 0" : '0px 0 80px 0', textAlign: "center"}}>
                    <button type="submit" className="base_type_btn"
                            style={{padding: "15px 100px", borderRadius: "3px", marginTop: "40px"}}
                            onClick={this.submit}>
                        <FormattedMessage id="submit"/>
                    </button>
                </div>
            </div>

        </div>
    }
}

class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <Form/>
            <Footer/>
        </div>
    }
}

class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <div style={{margin: "0 auto", display: "block", position: "relative", width: "1200px"}}>
                <Form/>
            </div>
            <Footer/>
        </div>
    }
}

export class Invitation extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };

    componentDidMount() {

    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default Invitation;


